import { Component } from '@angular/core';
import { AlertController,ModalController } from '@ionic/angular';
import { Token } from '../../Providers/Token';
import { CalculosPage } from '../calculos/calculos.page';
@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
    statusConsulta:boolean = false;
  constructor(
    public alertController: AlertController,
    private token: Token,
    public modalController: ModalController
  ) { }

  async presentAlertMultipleButtons() {   
    const alert = await this.alertController.create({
      header: 'Selecione uma opção.',
      mode: 'ios',
      buttons: [
        {
          text: 'Nova Consulta',
          handler: () => this.presentAlertPrompt('')
        },
        {
          text: 'Inserir Token',
          handler: () => this.presentAlertPrompt('')
        },
        {
          text: 'Fechar'
        },
      ]
    });

    await alert.present();
  }


  async presentAlertPrompt(tokenValue: String) {
    const _alert = await this.alertController.create({
      header: 'Insira o token do paciente.',
      message: 'Token fica localizado no prontuário médico.',
      mode: 'ios',
      cssClass: "alertInput",
      inputs: [
        {
          name: 'token',
          type: 'text',
          value: tokenValue,
          placeholder: 'Insira o Token'
        },
      ],
      buttons: [
        {
          text: 'QRCODE',
          handler: () => {
            this.token.scanToken().then(barcodeData => {
              this.presentAlertPrompt(barcodeData.text)
            }).catch(err => {
              this.presentAlert("Ocorreu um erro na leitura.",'Por favor, comunique ao suporte.')
            });
          }
        },
        {
          text: 'Iniciar',
          handler: (data) => {
            const isTokenValid = this.token.checkToken(data.token)
            if(isTokenValid)
                return this.presentModal(CalculosPage)

            return this.presentAlert("Token Inválido.",'Nenhum paciente foi encontrado.')
          }
        },
        {
          text: 'Fechar',
        },

      ]
    });

    await _alert.present();
  }

  async presentAlert(_header, _message) {
    const alert = await this.alertController.create({
      header: _header,
      message: _message,
      mode: 'ios',
      buttons: ['Fechar']
    });

    await alert.present();
  }

  async presentModal(_ModalPage) {
    this.statusConsulta = !this.statusConsulta
    const modal = await this.modalController.create({
      component: _ModalPage,
      cssClass: 'medium-modal-css'
    });
    return await modal.present();
  }

}
